import { Component, Input, TemplateRef, ViewEncapsulation } from '@angular/core';
import { MatCardModule } from '@angular/material/card';
import { NgClass, NgIf, NgTemplateOutlet } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';

export type DscCardTitleAvatarSize = 'large' | 'medium' | 'small';

export type DscCardVariant = 'neutral' | 'highlight';

export type DscCardAppearance = 'outlined' | 'raised';

export type DscCardTitleAvatar = {
  imgSrc?: string;

  icon?: string;

  size?: DscCardTitleAvatarSize;

  color?: string;
}

export type DscCardTitle = {
  variant?: DscCardVariant;

  text: string;

  subtext?: string;

  avatar?: DscCardTitleAvatar;
}

export type DscCardContent = {
  text?: string;

  template?: TemplateRef<any>;
}

export type DscCardImage = {
  src: string;

  alt: string;

  enableMargin?: boolean;

  topPosition?: boolean;
}

export type DscCardFooter = {
  showDivider?: boolean;

  template: TemplateRef<any>;
}

export type DscCardData = {
  appearance?: DscCardAppearance;

  interative?: boolean;

  focusable?: boolean;

  title?: DscCardTitle;

  image?: DscCardImage;

  content?: DscCardContent;

  footer?: DscCardFooter;
}

@Component({
  selector: 'dsc-card',
  standalone: true,
  imports: [MatCardModule, NgIf, NgTemplateOutlet, MatIconModule, NgClass],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-card.component.html',
  styleUrls: ['./dsc-card.component.scss']
})
export class DscCardComponent {
  @Input()
  data!: DscCardData;

  @Input() iconStyle: 'outlined' | 'filled' | 'rounded' | 'sharp' | 'two-tone' = 'filled';

  getIconClass(): string {
    if (this.iconStyle === 'filled') return 'material-icons';
    if (this.iconStyle === 'rounded') return 'material-icons-round';
    return `material-icons-${this.iconStyle}`;
  }
}
